@import url('https://fonts.googleapis.com/css?family=Open+Sans');
@import 'fluid';


*{
	margin: 0;
	padding: 0;
	&:focus{
outline: 0 !important;
    box-shadow: none !important;
	}
}
body {

	@include bg($loo_blk ,"../img/bg.jpg", top center, no-repeat, cover);
	font-family:$primary_font;
	color:$loo_blk;
	cursor: url("../img/vector.cur"), auto;

}
.btn-theme{	
@include border-radius(45px);
@include bg(transparent ,"", center, no-repeat, cover);
@include bdr(2px, $loo_red, solid);
@extend %btn-theme;	

}

.btn-theme-fill{
	@include border-radius(45px);
	@include bg(#eb3333 ,"", center, no-repeat, cover);
    font-size: 14px;
    font-weight: 900;
    padding: 12px 35px;
    text-transform: uppercase;
    color: #ffffff;
}
.interested-form{
.input-group {
	 border: 2px #e13539 solid;
    border-radius: 50px;
    padding: 3px;
    .mail-btn{
border-radius: 39px !important;
    padding: 9px 10px;
    font-size: 13px;
    background: #c9393c;
    color: #fff;
        border: 0;
    }
    input{
border: 0;
    height: 32px;
    background: transparent;
    font-weight: 700;
    font-size: 13px;
    padding: 0px 10px;
    color: #515151;
    }
    .send-btn{

    padding: 0;
    background: transparent;
    border: 0;
    }
    .btn-send{
font-size: 13px;
    color: rgb(255, 255, 255);
    padding: 6px 10px;
    background: rgb(201, 57, 60);
    border-radius: 39px !important;
  
    }
      &:hover{
      	.btn-send{
      	background: #000;
      	}
    }
}
}
main{
	margin-bottom:75px;
}
._cnt-white{
	color:$loo_w !important;
	
}
/* Menu */
._loocust-nav .navbar{
	padding: 25px 5% !important;

	li
	{ 
		position:relative;
	a
		{
			color:$loo_w;
			span
			{
				text-decoration: none;
				position: relative;
				display: inline-block;
				
			}
		}
		&:hover, &.current_page_item {
			&:after
				{
					content: "";
					position: absolute;
					bottom: 0;
					left: 0;
					right:0;
					border-top: 2px solid red;
					height: 45%;
					height: calc(50% - 1px);
					z-index: -1;
					width: 80%;
					margin:0 auto;					
					@include transition(width .2s);
				}
		}
		
	}
}

/* Menu End */

.strick {
	width: 70px;
	@include border-radius(30px);
	margin-bottom: 20px;
	border-bottom: 4px $loo_red solid;

}
._about_sec{
	padding-top: 75px;
}

/* product */
.inner{    padding-top: 75px;}
._prod_div
{
	.inner &{
		padding: 0px;

		.inner-title{    margin: 0;
    padding: 15px 0px;
			
			.tilte-style{    padding: 9px 25px;
    float: right;
    background: #dcdcdc;
    display: inline-block;
    border-right: 4px $loo_red solid;

			}}
}
	background:$loo_w;
	padding: 0px 3%;

	.navbar .navbar-nav 
	{
		li
		{
			.nav-link
			{
				font-size: 14px;
				margin: 0px 10px;
				color:$loo_blk;

				&:hover, &:active
				{
					color:$loo_red;
				}
							}
			&.active .nav-link
			{
				color:$loo_red;
			}
			&.active .nav-link._cnt-white{
				color:$loo_red !important;
			}
		}

	}
	/* Slider tab*/
	.tabbable
	{
		padding-top:3%;
		padding-bottom:25px;

		.nav.nav-tabs {
			float: left;
			display: block;
			margin-right: 10px;
			border: 0;			


			&:before{
				
			.inner &{
					display:none;
				}

				    content: " ";
    background: url(../img/zip-open.svg);
    background-size: cover;
    display: inline-block;
    width: 60px;
    height: 57px;
    left: -17px;
    bottom: -5px;
    position: relative;

			}

			li{
				@extend %display-flex;
.inner &{
	    height: 66px;
  border-left: 4px transparent solid;
   
}
				&.active, &:hover, &:active{
					.inner &{
						 background: #e2e1e1;
						 border-left: 4px $loo_red solid;
					}
					span{
						visibility: visible;
					}
				}
				a{
					.inner &{
						    padding-left: 20px;    padding-right: 20px;width: 100%;
					}
					@include align-items(center);
					@extend %display-inline-flex;
					color: $loo_blk;
					font-size:13px;
					font-weight: 700;
					text-decoration: none;
					embed{
						height: 5rem;
						margin-right: 10px;
					}
					span{
						.inner &{
							visibility:visible;
						}
						visibility:hidden;
					}
				}
			}
		}
		.prod_showcase{
			.img-bg{
				width: 400px;
      			height: 400px;
      			background: url("https://static.pexels.com/photos/60006/spring-tree-flowers-meadow-60006.jpeg") center;
				position: relative;
				display: block;
				margin: 0 auto;  
				background-size: cover;
      -webkit-clip-path: url(#prod_ptn);
      clip-path: url(#prod_ptn);
      @include transform(translateX(0));

  }

  ._prod_img{
  	width: 300px;
    height: 300px;
    text-align: center;
    margin: auto;
  	position: absolute;
  	bottom: 0;
  	left: 0;
  	right: 0;
  	top: 0;
  	img
  	{
  		height: 300px;
  	}
  }
}
/* product cnt */
